.header {
    position: fixed;
    top: 0;
    width: 100%;
    -webkit-box-shadow: 0 1px 3px 0 rgba(0,34,77,.1);
            box-shadow: 0 1px 3px 0 rgba(0,34,77,.1);
    background: #fff;
    z-index: 1;
}

.header .container{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;  
}

.header .logo {
    font-size: 20px;
    line-height: 50px;    
    text-decoration: none;
    font-weight: 800;
    color:#000;
}

.header nav {
    margin-left: 15px;
    display: inline-block;
}

.header nav .button{
    padding: 0;
    min-width: 0;
    min-height: 0;
}

.header nav .a{
    text-decoration: none;
    font-size: 15px;
    padding: 3px 15px;    
}

.header nav .selected{
    color: #333;
}

.header .right{
    -ms-flex: 1 1;
        flex: 1 1;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: end;
        justify-content: flex-end;    
    -ms-flex-align: center;    
        align-items: center;
}

.header .right .button{
    background: #0f88eb;
    padding: 0;
    min-width: 0;
    min-height: 0;
}

.header .right .a{
    text-decoration: none;
    font-size: 15px;
    color: #fff;
    padding: 7px;
}

.header .right .bell{
    margin: 0 20px;
}

.header .right .headimg{
    width: 38px;
    height: 38px;
    border-radius: 50%;
    cursor: pointer;
}



